import React, {useEffect, useState, useRef} from 'react'
import {Link, withRouter} from 'react-router-dom'
import {KTSVG} from '../../../_metronic/helpers'
import moment from 'moment'
import {mainConfig} from '../../../config/mainConfig'
import {Skeleton, Row, Col, Select as Select1, Pagination, Spin} from 'antd'
const {Option} = Select1
let arrIndex = 0,
  token = ''
function getWindowDimensions() {
  const {innerWidth: width, innerHeight: height} = window
  return {
    width,
    height,
  }
}

function ProductGrnWrapper(props: any) {
  const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions())
  const [loading, setloading] = useState<any>(false)
  const [loadingGrnTable, setloadingGrnTable] = useState<any>(false)
  const [loadingGrn, setloadingGrn] = useState<any>(false)
  const [ProductId, setProductId] = useState<any>('')
  const [grn, setgrn] = useState<any[]>([])
  const [prodHeader, setProdHeader] = useState<any>({})
  const [serched, setSearched] = useState<any[]>([])

  const [GrnSortQty, setGrnSortQty] = useState<any>('Asc')
  const [ProductSortCode, setProductSortCode] = useState<any>('Asc')
  const [ProductSortDesc, setProductSortDesc] = useState<any>('Asc')
  const [PackSortSize, setPackSortSize] = useState<any>('Asc')
  const [SupplierSortCode, setSupplierSortCode] = useState<any>('Asc')
  const [GrnSortNo, setGrnSortNo] = useState<any>('Asc')
  const [GrnSortPrice, setGrnSortPrice] = useState<any>('Asc')
  const [ReceiptSortDate, setReceiptSortDate] = useState<any>('Asc')

  const [defaultColumnName, setdefaultColumnName] = useState<any>('Receipt_Date')
  const [pageNumber, setPageNumber] = useState<any>(1)
  const [sortFlag, setSortFlag] = useState<any>(0)
  const [rowCount, setRowCount] = useState<any>(0)

  useEffect(() => {
    if (props.location.state != undefined) {
      localStorage.setItem('productId', props.location.state.prod_id)
    }

    setProductId(
      props.location.state != undefined
        ? props.location.state.prod_id
        : localStorage.getItem('productId')
    )
    let prodData =
      props.location.state != undefined
        ? props.location.state.prod_id
        : localStorage.getItem('productId')

    getData2(prodData, pageNumber, defaultColumnName, sortFlag)

    const handleResize=()=> {
      setWindowDimensions(getWindowDimensions())
    }

    window.addEventListener('resize', handleResize)
    return () => window.removeEventListener('resize', handleResize)
  }, [])

  const getData2=(data: any, pageNumber: any, defaultColumnName: any, sortFlag: any)=> {
  
    setloadingGrnTable(true)

    try {
      fetch(
        `${mainConfig.MAIN_ENDPOINT}/api/get-prod-grn/${
          data != undefined ? data : 868
        }/${pageNumber}/${defaultColumnName}/${sortFlag}`,
        {
          method: 'get',
          headers: {
            Authorization: `Bearer ${token}`,
            Accept: 'application/json',
            'Content-Type': 'application/json',
          },
        }
      )
        .then(function (response) {
          if (response.status != 200) {
            localStorage.clear()
            window.location.reload()
          }

          response.json().then(function (data) {
            setgrn(data.data.grn)
            setProdHeader(data.data.header)
            setRowCount(data.data.rowCount)
            setloadingGrn(false)
            setloadingGrnTable(false)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {
      console.log('ERR>>>', err)
    }
  }

  const onSelectProduct=(e: any)=> {
    setloading(true)
    setSearched([])

    let n = e

    setProductId(n != undefined ? n.prod_id : 868)
    setSearched([])

    setTimeout(() => {
      getData2(n, pageNumber, defaultColumnName, sortFlag)
    }, 1000)

    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/get-prod-grn/${n.prod_id}`, {
        
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (response) {
          if (response.status != 200) {
            localStorage.clear()
            window.location.reload()
          }

          response.json().then(function (data) {
            setgrn(data.data.grn)
            setProdHeader(data.data.header)
            setSearched([])
            setloading(false)
          })
        })
        .catch(function (err) {
          console.log('Fetch Error :-S', err)
        })
    } catch (err) {
      console.log('ERR>>>', err)
    }
  }

  const onSortGrnTable=(data: any)=> {
    setdefaultColumnName(data)
    sortFlag == 0 ? setSortFlag(1) : setSortFlag(0)

    switch (data) {
      case 'Grn_Qty':
        if (GrnSortQty == 'Asc') {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setGrnSortQty('Desc')
        } else {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setGrnSortQty('Asc')
        }
        break
      case 'Product_Code':
        if (ProductSortCode == 'Asc') {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setProductSortCode('Desc')
        } else {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setProductSortCode('Asc')
        }
        break
      case 'Product_Desc':
        if (ProductSortDesc == 'Asc') {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setProductSortDesc('Desc')
        } else {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setProductSortDesc('Asc')
        }
        break
      case 'Pack_Size':
        if (PackSortSize == 'Asc') {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setPackSortSize('Desc')
        } else {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setPackSortSize('Asc')
        }
        break
      case 'Supplier_Code':
        if (SupplierSortCode == 'Asc') {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setSupplierSortCode('Desc')
        } else {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setSupplierSortCode('Asc')
        }
        break
      case 'Grn_No':
        if (GrnSortNo == 'Asc') {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setGrnSortNo('Desc')
        } else {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setGrnSortNo('Asc')
        }
        break
      case 'Grn_Price':
        if (GrnSortPrice == 'Asc') {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setGrnSortPrice('Desc')
        } else {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setGrnSortPrice('Asc')
        }
        break
      case 'Receipt_Date':
        if (ReceiptSortDate == 'Asc') {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setReceiptSortDate('Desc')
        } else {
          getData2(localStorage.getItem('productId'), pageNumber, data, sortFlag)
          setReceiptSortDate('Asc')
        }
        break
    }
  }
  const handlePagination=(page: any)=> {
    let prodData =
      props.location.state != undefined
        ? props.location.state.prod_id
        : localStorage.getItem('productId')

    getData2(prodData, page, defaultColumnName, sortFlag)
    setPageNumber(page)
  }
  token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')

  return (
    <React.Fragment>
      {loadingGrn == true ? (
        <React.Fragment>
          <Skeleton loading={loadingGrn} active></Skeleton>
          <Skeleton loading={loadingGrn} active></Skeleton>
          <Skeleton loading={loadingGrn} active></Skeleton>
        </React.Fragment>
      ) : (
        <React.Fragment>
          {windowDimensions.width > 850 ? (
            <div className='row gy-5 gx-xl-4'>
              <div className={`card`}>
                {serched.length > 0 ? (
                  <div className={`card`} tabIndex={1}>
                    <div className='table-responsive h-350px'>
                      {serched.map((j) => {
                        return (
                          <div className='align-items-center mb-2 p-2'>
                            <div className='flex-grow-1 '>
                              <button
                                type='button'
                                className='btn btn-md btn-light-primary'
                                onClick={() => onSelectProduct(j)}
                              >
                                {j.parent_product_code} &nbsp;&nbsp;&nbsp;&nbsp;{' '}
                                {j.clean_description}
                              </button>
                            </div>
                          </div>
                        )
                      })}
                    </div>
                  </div>
                ) : null}
              </div>
            </div>
          ) : null}

          <Row style={{marginTop: '4%'}}>
            <Col span={20}>
              <ol className='breadcrumb breadcrumb-dot fs-6 fw-bold' style={{margin: '1%'}}>
                <Link to='/home' className='pe-1'>
                  <li className='breadcrumb-item pe-1'>Home</li>
                </Link>
                / &nbsp;
                <Link to={{pathname: '/product', state: {prod_id: ProductId, data: prodHeader}}}>
                  <li className='breadcrumb-item pe-1'>Product</li>
                </Link>
                /&nbsp;<li className='breadcrumb-item pe-3'>GRN</li>
              </ol>
            </Col>

            <Col>
              <Link to={{pathname: '/product', state: {prod_id: ProductId, data: prodHeader}}}>
                <button className='btn btn-sm btn-primary '>Back To Product</button>
              </Link>
            </Col>
          </Row>

          {windowDimensions.width > 800 ? (
            <React.Fragment>
              
                <div className='toolbar' id='kt_toolbar'>
                  <div id='kt_toolbar_container' className='container-fluid d-flex flex-stack'>
                    <div className='d-flex align-items-center py-5 justify-content-space-between '>
                      <a
                        className='btn btn-outline btn-outline-dashed m-1'
                        style={{cursor: 'text'}}
                      >
                        <span
                          className='card-label fs-2 mb-1 text-purple header-custom'
                          style={{color: 'rgb(138, 38, 80)'}}
                        >
                          {prodHeader.parent_product_code}{' '}
                          <span className='text-muted mt-1 fw-bold fs-8 header-custom-2 '>
                            Aggregate Code
                          </span>
                        </span>
                      </a>
                    </div>
                    {prodHeader.clean_description == '' ? (
                      <div className='d-flex align-items-center py-5 justify-content-space-between'></div>
                    ) : (
                      <div className='d-flex align-items-center py-5 justify-content-space-between'>
                        <a
                          className='btn btn-outline btn-outline-dashed m-1'
                          style={{cursor: 'text'}}
                        >
                          {' '}
                          <span
                            className='card-label fs-2 mb-1 text-purple header-custom'
                            style={{color: 'rgb(138, 38, 80)'}}
                          >
                            {prodHeader.clean_description}
                          </span>
                        </a>
                      </div>
                    )}

                    <div className='d-flex align-items-center py-5 justify-content-space-between'>
                      <a
                        className='btn btn-outline btn-outline-dashed m-1'
                        style={{cursor: 'text'}}
                      >
                        <span
                          className='card-label fs-2 mb-1 text-purple header-custom'
                          style={{color: 'rgb(138, 38, 80)'}}
                        >
                          {prodHeader.dt_pack}{' '}
                          <span className='text-muted mt-1 fw-bold fs-8 header-custom-2'>
                            DT Pack
                          </span>
                        </span>
                      </a>
                    </div>
                  </div>
                </div>
             
            </React.Fragment>
          ) : null}

          <div className='row gy-5 g-xl-10' style={{marginTop: '20px'}}></div>

          <div className='row g-5 gx-xl-8'>
            <div className='col-xl-12'>
              <div className='card-xxl-stretch mb-4 mb-xxl-7'>
                <div className='row gy-5 g-xl-4'>
                  <div className={`card`}>
                    <div className='card-header border-0 pt-2'>
                      <h3 className='card-title align-items-start flex-column'>
                        <span
                          className='card-label fw-bolder fs-3 mb-1'
                          style={{color: 'rgb(138, 38, 80)'}}
                        >
                          GRN
                        </span>
                      </h3>
                      <div className='card-toolbar'></div>
                    </div>

                    <div className='card-body py-3 table-align-left'>
                      <div className='table-responsive h-600px table-scroll'>
                        <table className='table table-row-dashed table-row-gray-300 align-middle gs-0 gy-4 main-table'>
                          <thead>
                            <tr className='fw-bolder text-muted'>
                              <th
                                className='min-w-100px cursor-pointer'
                                onClick={() => {
                                  onSortGrnTable('Product_Code')
                                }}
                              >
                                Product Code
                                {ProductSortCode != 'Desc' ? (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr062.svg'}
                                  />
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                              <th
                                className='min-w-100px cursor-pointer'
                                onClick={() => {
                                  onSortGrnTable('Product_Desc')
                                }}
                              >
                                Product Description
                                {ProductSortDesc != 'Desc' ? (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr062.svg'}
                                  />
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                              <th
                                className='min-w-100px cursor-pointer'
                                onClick={() => onSortGrnTable('Pack_Size')}
                              >
                                Pack Size
                                {PackSortSize != 'Desc' ? (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr062.svg'}
                                  />
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                              <th
                                className='min-w-100px cursor-pointer'
                                onClick={() => onSortGrnTable('Supplier_Code')}
                              >
                                Supplier Code
                                {SupplierSortCode != 'Desc' ? (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr062.svg'}
                                  />
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                              <th className='min-w-100px cursor-pointer' onClick={() => onSortGrnTable('Grn_No')}>
                                GRN No
                                {GrnSortNo != 'Desc' ? (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr062.svg'}
                                  />
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                              <th
                                className='min-w-70px'
                                onClick={() => onSortGrnTable('Grn_Price')}
                              >
                                GRN Price
                                {GrnSortPrice != 'Desc' ? (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr062.svg'}
                                  />
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                              <th className='min-w-70px ' onClick={() => onSortGrnTable('Grn_Qty')}>
                                GRN Qty
                                {GrnSortQty != 'Desc' ? (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr062.svg'}
                                  />
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                              <th
                                className='min-w-100px cursor-pointer '
                                onClick={() => onSortGrnTable('Receipt_Date')}
                              >
                                Reciept Date
                                {ReceiptSortDate != 'Desc' ? (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr062.svg'}
                                  />
                                ) : (
                                  <KTSVG
                                    className='text-hover-primary'
                                    path={'/media/icons/duotune/arrows/arr068.svg'}
                                  />
                                )}
                              </th>
                            </tr>
                          </thead>
                          {loadingGrnTable == true ? (
                            <div className='spin_example'>
                              <Spin size='large' tip='Loading...' spinning={loadingGrnTable} />
                            </div>
                          ) : (
                            <tbody>
                              {grn != undefined &&
                                grn.map((j) => {
                                  return (
                                    <tr>
                                      <td>
                                        <Link
                                          to={{
                                            pathname: '/product',
                                            state: {prod_id: j.Product_Id_Main, data: prodHeader},
                                          }}
                                        >
                                          <span className='text-danger fw-bolder text-hover-primary d-block fs-6 text-decoration-underline link'>
                                            {j.Product_Code}
                                          </span>
                                        </Link>
                                      </td>
                                      <td>
                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                          {j.Product_Desc}
                                        </span>
                                      </td>
                                      <td>
                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                          {j.Pack_Size}
                                        </span>
                                      </td>

                                      <td>
                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                          {j.Supplier_Id_Main} {j.Supplier_Code}
                                        </span>
                                      </td>

                                      <td>
                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                          {j.Grn_No}
                                        </span>
                                      </td>
                                      <td>
                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                          {j.Grn_Price.charAt(0) == '.'
                                            ? '0' + j.Grn_Price
                                            : j.Grn_Price}
                                        </span>
                                      </td>

                                      <td>
                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                          {j.Grn_Qty}
                                        </span>
                                      </td>

                                      <td>
                                        <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                          {moment(j.Receipt_Date).format('DD-MMM-YYYY')}
                                        </span>
                                      </td>
                                    </tr>
                                  )
                                })}
                            </tbody>
                          )}
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <Pagination
              defaultCurrent={pageNumber}
              size='small'
              defaultPageSize={30}
              total={rowCount}
              onChange={handlePagination}
              style={{margin: '2%'}}
              showQuickJumper
              hideOnSinglePage={true}
              showSizeChanger={false}
            />
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default withRouter(ProductGrnWrapper)
